import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { useMediaQuery, Grid } from '@material-ui/core';
import { useSnackbar } from 'notistack';

import ContainedButton from 'components/UI/Buttons/ContainedButton';
import TradingViewWidget, { Themes } from 'react-tradingview-widget';
import SectionHeader from 'components/UI/SectionHeader';

const useStyles = makeStyles(theme => ({
    root: {},
    image: {
        boxShadow:
            '25px 60px 125px -25px rgba(80,102,144,.1), 16px 40px 75px -40px rgba(0,0,0,.2)',
        borderRadius: theme.spacing(2),
        [theme.breakpoints.down('sm')]: {
            maxWidth: 500,
        },
    },
    tradingBoard: {
        width: '100%',
        height: '100%',
        [theme.breakpoints.down('sm')]: {
            height: 360,
            width: '100%'
        },
    },
    buyMarsButton: {
        backgroundColor: theme.palette.error.light
    }
}));

const Hero = props => {
    const { setIsSwapDialog, account, className, ...rest } = props;
    const classes = useStyles();
    const theme = useTheme();
    const isMd = useMediaQuery(theme.breakpoints.up('md'), {
        defaultMatches: true,
    });
    const { enqueueSnackbar } = useSnackbar();

    const buyTokenHandler = () => {
        if (Math.floor((new Date()).getTime() / 1000) > 1627131600) {
            if (account) {
                setIsSwapDialog(true)
            }
            else {
                enqueueSnackbar('Please connect your wallet', { variant: 'warning' })
            }
        }
        else {
            enqueueSnackbar('Presale is not started yet!', { variant: 'warning' })
        }
    }

    return (
        <div className={clsx(classes.root, className)} {...rest}>
            <Grid
                container
                justify="space-between"
                spacing={4}
                direction={isMd ? 'row' : 'column-reverse'}
            >
                <Grid
                    item
                    container
                    justify="flex-start"
                    alignItems="center"
                    xs={12}
                    md={6}
                    data-aos={'fade-up'}>
                    <div className={classes.tradingBoard}>
                        <TradingViewWidget
                            symbol="BNB"
                            theme={Themes.DARK}
                            locale="fr"
                            autosize />
                    </div>
                </Grid>
                <Grid
                    item
                    container
                    alignItems="center"
                    xs={12}
                    md={5}
                    data-aos={'fade-up'}>
                    <SectionHeader
                        title={
                            <div style={{ display: 'flex', flexDirection: 'column', width: '100%' }}>
                                <span style={{ color: theme.palette.error.light }}>
                                    Penalty fee
                                    <br />
                                </span>
                                <span style={{ color: theme.palette.text.primary, fontSize: 18, fontWeight: '300', textAlign: 'justify', lineHeight: 1.8 }}>
                                    Each sell transaction on our platform will be penalized with a <b>8% fee.</b> This in order to encourage users to holding, avoid massive sales and speculators. Making the price of <b>SMARS</b> increase. Also a 2% penalty fee for those who unstake within 3 days. Win or Win: Sellers are penalized with a 8% penalty fee. Holders win, they receive the penalty fee distribution directly to their wallet.
                                    <br />
                                    <br />
                                </span>
                                <span style={{ color: theme.palette.text.primary, fontSize: 18, fontWeight: '300', textAlign: 'justify', lineHeight: 1.8 }}>
                                    This fee will be distributed to those who still hold SMARS, increasing their holdings. (Rewards).
                                    <br />
                                    <br />
                                </span>
                            </div>
                        }
                        // subtitle="Superpowers for Any Currency"
                        ctaGroup={[
                            <ContainedButton className={classes.buyMarsButton} onClick={buyTokenHandler} variant="contained" size="large">
                                BUY SMARS NOW!
                            </ContainedButton>,
                            // <ContainedButton variant="outlined" color="primary" size="large">
                            //     Learn more
                            // </ContainedButton>,
                        ]}
                        align={isMd ? "left" : 'center'}
                        disableGutter
                        titleVariant="h3"
                    />
                </Grid>
            </Grid>
        </div>
    );
};

Hero.propTypes = {
    /**
     * External classes
     */
    className: PropTypes.string,
};

export default Hero;
