
import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { useMediaQuery, Grid } from '@material-ui/core';

import ContainedButton from 'components/UI/Buttons/ContainedButton';
import Image from 'components/UI/Image';
import TimeCount from 'components/UI/TimeCount';
import SectionHeader from 'components/UI/SectionHeader';

const useStyles = makeStyles(theme => ({
  root: {
  },
  image: {
    boxShadow:
      '25px 60px 125px -25px rgba(80,102,144,.1), 16px 40px 75px -40px rgba(0,0,0,.2)',
    borderRadius: theme.spacing(2),
    [theme.breakpoints.down('sm')]: {
      maxWidth: 500,
    },
  },
  lastGrid: {
    [theme.breakpoints.up('sm')]: {
      // marginTop: '40%',
    },
  },
}));

const Customization = props => {
  const { className, ...rest } = props;
  const classes = useStyles();

  const theme = useTheme();
  const isMd = useMediaQuery(theme.breakpoints.up('md'), {
    defaultMatches: true,
  });


  const viewHandler = () => {
    window.open('https://safe-mars.gitbook.io/safemars/', '_blank');
  }

  return (
    <div className={clsx(classes.root, className)} {...rest}>
      <SectionHeader
        subtitleColor='textPrimary'
        label="Investment Diversification"
        title="White Paper"
        subtitle="SafeMars Finance a decentralized exchange running on Binance Smart Chain and Pancake Swap Exchange fork, with lots of other features that let you win tokens. Just holding our token you get profits, and receive dividends in BUSD, rewards, penalty fee distribution, and more. But if you want more you can do staking and farming with good returns and low risk."
        align="center"
        ctaGroup={[
          <ContainedButton onClick={viewHandler} variant="outlined" style={{ backgroundColor: theme.palette.error.light, border: 'none' }} size="large">
            View
          </ContainedButton>,
          // <ContainedButton variant="outlined" color="primary" size="large">
          //   Learn more
          // </ContainedButton>,
        ]}
      />
      <Grid container spacing={isMd ? 4 : 2}>
        <Grid item xs={12} sm={6}>
          <Grid container justify="center" alignItems="center">
            <Image
              src="assets/images/defi1.jpg"
              alt="A Funding Portal"
              className={classes.image}
              data-aos="fade-up"
            />
          </Grid>
          <SectionHeader
            subtitleColor='textPrimary'
            title = ''
            titleColor='textPrimary'
            // label="Upcoming Defi UX Design"
            align="center"
            ctaFullWidth
            ctaGroup={[
              <TimeCount deadTimeStamp = {1627473600} title = 'DeFi and Dex' subTitle = 'launch in'/>
            ]}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
        <SectionHeader
            subtitleColor='textPrimary'
            titleColor='error'
            // label="Upcoming Defi UX Design"
            title="Launch Time"
            titleVariant = 'h3'
            subtitle="Our Yield Farming DeFi platform will be launched after 2 days once presale has done without any delay!"
            align="center"
          />
          <Grid
            container
            justif="center"
            alignItems="center"
            className={classes.lastGrid}
          >
            <Image
              src="assets/images/dex1.jpg"
              alt="A Funding Portal"
              className={classes.image}
              data-aos="fade-up"
            />
          </Grid>
        </Grid>
      </Grid>
    </div>
  );
};

Customization.propTypes = {
  /**
   * External classes
   */
  className: PropTypes.string,
};

export default Customization;
