import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { useMediaQuery, Grid, colors } from '@material-ui/core';
import { useSnackbar } from 'notistack';

import ContainedButton from 'components/UI/Buttons/ContainedButton';
import Image from 'components/UI/Image';
import SectionHeader from 'components/UI/SectionHeader';
import TimeCount from 'components/UI/TimeCount';

const useStyles = makeStyles(theme => ({
  root: {},
  image: {
    boxShadow:
      '25px 60px 125px -25px rgba(80,102,144,.1), 16px 40px 75px -40px rgba(0,0,0,.2)',
    borderRadius: theme.spacing(2),
    height: 480,
    width: 480,
    [theme.breakpoints.down('sm')]: {
      maxWidth: 500,
      marginBottom: 60
    },
  },
  mobileImageContainer: {
    [theme.breakpoints.down('sm')]: {
      position: 'absolute', left: 0, marginTop: 80,
    },
    position: 'absolute', right: 0, marginTop: 80,
  },
  buyMarsButton: {
    backgroundColor: theme.palette.error.light
  },
  textBoard: {
    backgroundColor: 'rgb(82 57 57 / 50%)',
    padding: theme.spacing(2.5),
    borderRadius: theme.spacing(1)
  }
}));

const Hero = props => {
  const { setIsSwapDialog, account, className, ...rest } = props;
  const classes = useStyles();
  const theme = useTheme();
  const isMd = useMediaQuery(theme.breakpoints.up('md'), {
    defaultMatches: true,
  });
  const { enqueueSnackbar } = useSnackbar();

  const getTokenHandler = () => {
    if (Math.floor((new Date()).getTime() / 1000) > 1627131600) {
      if (account) {
        setIsSwapDialog(true)
      }
      else {
        enqueueSnackbar('Please connect your wallet', { variant: 'warning' })
      }
    }
    else {
      enqueueSnackbar('Presale is not started yet!', { variant: 'warning' })
    }
  }

  return (
    <div className={clsx(classes.root, className)} {...rest}>
      <Grid
        container
        justify="space-between"
        spacing={4}
        direction={isMd ? 'row' : 'column-reverse'}
      >
        <Grid
          item
          container
          alignItems="center"
          xs={12}
          md={6}
          data-aos={'fade-up'}
          className={classes.textBoard}
        >
          <SectionHeader
            title={
              <div style={{ display: 'flex', flexDirection: 'column' }}>
                <span style={{ color: theme.palette.text.primary, fontSize: 18 }}>
                  Presale for
                </span>
                <span style={{ color: theme.palette.error.light }}>
                  SAFE MARS    <Image
                    src="assets/images/SMARS.png"
                    alt="Web3 Legal Engineering"
                    height={32}
                    width={32}
                    data-aos="zoom-in-down"
                    data-aos-easing="ease-out-cubic"
                    data-aos-duration="2000"
                  />
                  <br />
                </span>
                <span style={{ color: theme.palette.text.primary, fontSize: 18, fontWeight: '300', textAlign: 'justify', lineHeight: 1.8 }}>
                  You can buy <b> Safe Mars</b> token (SMARS) through our Presale platform and can swap <b> SMARS </b> by using our upcoming dex or pancakeswap later.
                  <br />
                  <br />
                </span>
                <span style={{ color: theme.palette.text.primary, fontSize: 18, fontWeight: '300', textAlign: 'justify', lineHeight: 1.8 }}>
                  Buy <b>SMARS</b> at the start of the launch. Sell when the price becomes high enough to fulfill your greedy desire.
                  <br />
                  <br />
                </span>
                <span style={{ color: theme.palette.text.primary, fontSize: 20 }}>
                  The Probability of <b>SMARS</b> dropping below the initial price of $3.
                  <br />
                  <br />
                </span>
                <span style={{ color: theme.palette.text.primary, fontSize: 18, fontWeight: '300', textAlign: 'justify', lineHeight: 1.8 }}>
                  Smart Investors stake <b>SMARS</b> long-term on the DeFi platform.(<b>DeFi is coming soon!</b>)
                  The APY of SMARS Pool is amazing and real! The meaning of <b>SMARS</b> Pool's APY 100% is if you buy $100 worth of <b>SMARS</b> now and stake it for 1 year, you will receive $100 worth of BUSD.
                </span>
                <span style={{ color: colors.cyan[500], fontSize: 18, fontWeight: 'bold', textAlign: 'justify', lineHeight: 1.8 }}>
                  Presale period : July 19th - 24th
                </span>
                <span style={{ color: colors.deepOrange[500], fontSize: 18, fontWeight: 'bold', textAlign: 'justify', lineHeight: 1.8 }}>
                The Presale winner will get a lot of rewards in Safe Mars token Or BUSD.
                </span>
              </div>
            }
            // subtitle="Superpowers for Any Currency"
            ctaGroup={[
              <ContainedButton className={classes.buyMarsButton} onClick={getTokenHandler} variant="contained" size="large">
                BUY SMARS NOW!
              </ContainedButton>,
              // <ContainedButton variant="outlined" color="primary" size="large">
              //   Learn more
              // </ContainedButton>,
            ]}
            align={isMd ? "left" : 'center'}
            disableGutter
            titleVariant="h3"
          />
        </Grid>
        <Grid
          item
          container
          justify="flex-end"
          alignItems="center"
          xs={12}
          md={6}
          data-aos={'fade-up'}
        >
          <TimeCount deadTimeStamp={1626696000} title='Presale' subTitle='start in' />
          <Image
            src="assets/images/MARS.png"
            alt="Web3 Legal Engineering"
            className={classes.image}
            data-aos="fade-right"
            data-aos-easing="ease-out-cubic"
            data-aos-duration="2000"
          />
          {/* <div className={classes.mobileImageContainer}>
            <Image
              src="assets/images/marketMobileDashboard.png"
              alt="Web3 Legal Engineering"
              className={classes.image}
              data-aos="fade-left"
              data-aos-easing="ease-out-cubic"
              data-aos-duration="2000"
            />
          </div> */}
        </Grid>
      </Grid>
    </div>
  );
};

Hero.propTypes = {
  /**
   * External classes
   */
  className: PropTypes.string,
};

export default Hero;
