import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import { Card, CardContent, Grid, Typography, colors } from '@material-ui/core';
// import { LazyLoadImage } from 'react-lazy-load-image-component';
import 'react-lazy-load-image-component/src/effects/blur.css';
import 'react-lazy-load-image-component/src/effects/opacity.css';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import FiberManualRecordIcon from '@material-ui/icons/FiberManualRecord';

import { LOAD_MAP_LIST1, LOAD_MAP_LIST2, LOAD_MAP_LIST3, LOAD_MAP_LIST4 } from 'constants/common';
import { isEmpty } from 'utils/utility';

const useStyles = makeStyles(theme => ({
    root: {
        width: '100%',
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: 'center',
    },
    image: {
        minHeight: 1048,
        position: 'absolute',
        objectFit: 'cover',
        [theme.breakpoints.down('sm')]: {
            width: 'auto',
        },
    },
    centerImage: {
        objectFit: 'cover',
        [theme.breakpoints.down('sm')]: {
            width: 'auto',
        },
    },
    rectangleImage: {
        position: 'absolute',
        marginTop: theme.spacing(17)
    },
    leftImage: {
        marginBottom: theme.spacing(-12),
        marginLeft: theme.spacing(-6),
    },
    topImage: {
        marginBottom: theme.spacing(-14),
        marginRight: theme.spacing(10)
    },
    belowImage: {
        marginTop: theme.spacing(-12),
        marginRight: theme.spacing(10),
    },
    card: {
        width: '575px',
        cursor: 'pointer',
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: "center",
        boxShadow: '2px 4px 13px 4px rgba(0, 0, 0, 0.25)',
        backgroundColor: 'rgb(82 57 57 / 50%)',
        padding: theme.spacing(2.5),
        transition: 'ease-out 0.4s',
        borderRadius: '20px',
        '&:hover': {
            transform: 'translateY(-5px)',
            transition: `ease-out 0.4s `,
            backgroundColor: theme.palette.background.third,
            opacity: '100%'
        },
    },
    avatar: {
        width: 54,
        height: 58
    },
    mainTitle: {
        color: theme.palette.text.third,
        fontWeight: 'bold',
        textShadow: '0px 4px 3px #252628'
    },
    cardTitle: {
        fontSize: '30px',
        fontWeight: 'bold',
        color: theme.palette.text.sixth,
        marginBottom: theme.spacing(1)
    },
    subText: {
        display: 'flex',
        alignItems: 'center',
        fontSize: '25px',
        color: theme.palette.text.fourth,
        marginBottom: theme.spacing(2.5)
    },
    genernalCardFont: {
        fontSize: '30px',
        // color: theme.custom.palette.green,
        color: colors.deepOrange[500],
        borderBottom: `1px solid ${theme.custom.palette.green}`
    },
    genernalConetentFont: {
        fontSize: '20px',
        marginTop: theme.spacing(1),
        marginBottom: theme.spacing(1),
        color: theme.palette.text.primary
    },
    subTitleFont: {
        fontSize: '28px',
        color: theme.palette.text.primary
    },
    titleContainer: {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: 'center',
        color: theme.custom.palette.purple
    },
    specialMargin: {
        marginBottom: theme.spacing(3)
    },
    specialMarginTop: {
        marginTop: theme.spacing(4)
    },
    blendImage: {
        position: 'absolute',
        marginTop: theme.spacing(12),
        zIndex: 0
    },
    titleCaption: {
        // color: colors.deepOrange[500],
        color : theme.palette.error.main,
        fontWeight:'bold',
        marginBottom: -40,
        [theme.breakpoints.down('sm')]: {
            marginBottom: 0,
        }
    },
    q3Board: {
        zIndex: 1,
        marginTop: 200,
        [theme.breakpoints.down('sm')]: {
            marginTop: 32,
        }
    },
    q4board: {
        zIndex: 1, marginTop: 120,
        [theme.breakpoints.down('sm')]: {
            marginTop: 32
        }
    }
}));

const Roadmap = props => {
    const { className, ...rest } = props;
    const classes = useStyles();

    return (
        <div>
            <Typography variant='h3' className={classes.titleCaption} align='center'>Road Map</Typography>
            <Grid container direction={'row'} className={clsx(classes.root, className)} {...rest}>
                <Grid container item md={6} xs={12} justify='flex-start'>
                    <Card className={classes.card}>
                        <CardContent style={{ width: '100%' }}>
                            <Typography variant='h6' className={classes.genernalCardFont}>
                                Q2 2021
                            </Typography>
                            {LOAD_MAP_LIST1.map((item, index) => {

                                return (
                                    <React.Fragment key={index}>
                                        {!isEmpty(item.subTitles) ?
                                            <div>
                                                <div className={classes.titleContainer}>
                                                    <Typography component='div' variant='body1' className={classes.genernalConetentFont}>
                                                        {item?.title}
                                                    </Typography>
                                                    <CheckCircleIcon fontSize='large' />
                                                </div>
                                                <div style={{ marginLeft: 0 }}>
                                                    {
                                                        item.subTitles.map((sutTitle, index) => {

                                                            return (
                                                                <Grid key={index} container item xs={12} justify='flex-end'>
                                                                    <Grid container item xs={6} alignItems='center' >
                                                                        <FiberManualRecordIcon  style={{ marginRight: 8 }} />
                                                                        <Typography className={classes.subTitleFont} style={{ marginLeft: 0 }}>
                                                                            {sutTitle}
                                                                        </Typography>
                                                                    </Grid>
                                                                </Grid>
                                                            )
                                                        })
                                                    }
                                                </div>
                                            </div>
                                            :
                                            <div className={classes.titleContainer}>
                                                <Typography component='div' variant='body1' className={classes.genernalConetentFont}>
                                                    {item?.title}
                                                </Typography>
                                                <CheckCircleIcon style = {{color: colors.green[`${item.progress}`]}}  fontSize='large' />
                                            </div>
                                        }
                                    </React.Fragment>
                                )
                            })}
                        </CardContent>
                    </Card>
                </Grid>
                <Grid container item md={6} xs={12} justify='flex-start' className={classes.q3Board} >
                    <Card className={classes.card}>
                        <CardContent style={{ width: '100%' }}>
                            <Typography variant='h6' className={classes.genernalCardFont}>
                                Q3 2021
                            </Typography>
                            {LOAD_MAP_LIST2.map((item, index) => {

                                return (
                                    <div className={classes.titleContainer} key={index}>
                                        <Typography component='div' variant='body1' className={classes.genernalConetentFont}>
                                            {item?.title}
                                        </Typography>
                                        <CheckCircleIcon fontSize='large' style = {{color: colors.green[`${item.progress}`]}}  />
                                    </div>
                                )
                            })}
                        </CardContent>
                    </Card>
                </Grid>
            </Grid>
            <Grid container className={clsx(classes.root, classes.specialMarginTop)} {...rest}>
                <Grid container item md={6} xs={12} justify='center' >
                    <Card className={classes.card}>
                        <CardContent style={{ width: '100%' }}>
                            <Typography variant='h6' className={classes.genernalCardFont}>
                                Q4 2021
                            </Typography>
                            {
                                LOAD_MAP_LIST3.map((item, index) => {

                                    return (
                                        <div className={classes.titleContainer} key={index} >
                                            <Typography component='div' variant='body1' className={classes.genernalConetentFont}>
                                                {item?.title}
                                            </Typography>
                                            <CheckCircleIcon fontSize='large' style = {{color: colors.green[`${item.progress}`]}} />
                                        </div>
                                    )
                                })
                            }
                        </CardContent>
                    </Card>
                </Grid>
                <Grid container item md={6} xs={12} justify='center' className={classes.q4board}>
                    <Card className={classes.card}>
                        <CardContent style={{ width: '100%' }}>
                            <Typography variant='h6' className={classes.genernalCardFont}>
                                Q1 2022
                            </Typography>
                            {
                                LOAD_MAP_LIST4.map((item, index) => {

                                    return (
                                        <div className={classes.titleContainer} key={index}>
                                            <Typography component='div' variant='body1' className={classes.genernalConetentFont}>
                                                {item?.title}
                                            </Typography>
                                            <CheckCircleIcon fontSize='large' style = {{color: isEmpty(item.progress)? colors.grey[600] : colors.green[`${item.progress}`]}} />
                                        </div>
                                    )
                                })
                            }
                        </CardContent>
                    </Card>
                </Grid>
            </Grid>
        </div>
    );
};

export default Roadmap;
