
import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { useMediaQuery, Grid } from '@material-ui/core';

import LearnMoreLink from 'components/LearnMoreLink';
import Image from 'components/UI/Image';
import SectionHeader from 'components/UI/SectionHeader';
import CardBase from 'components/UI/CardBase';

const useStyles = makeStyles(theme => ({
  root: {
    cursor:'pointer'
  },
  logo: {
    maxWidth: 50
  },
}));

const Partners = props => {
  const { data, className, ...rest } = props;
  const classes = useStyles();

  const theme = useTheme();
  const isMd = useMediaQuery(theme.breakpoints.up('md'), {
    defaultMatches: true,
  });
  const linkHandler = () => {
    window.open('https://safe-mars.gitbook.io/safemars/', '_blank');
  }

  return (
    <div className={clsx(classes.root, className)} {...rest}>
      <Grid container spacing={isMd ? 4 : 2}>
        <Grid item xs={12} md={6} data-aos="fade-up">
          <SectionHeader
            title="SAFE MARS Finance investment opportunities."
            subtitle="Safe-Mars token holders benefit from passive rewards through static reflection, auto liquidity, airdrop, and auto burn.
            Proprietary anti-whale mechanisms built into the contract stop the supply from being controlled by syndicates."
            align="left"
            label="Dynamic SAFE MARS Finance Investment Opportunities"
            ctaGroup={[
              <LearnMoreLink
                title="See all opportunities"
                onClick = {linkHandler}
                href="#"
                variant="h6"
              />
            ]}
            disableGutter
          />
        </Grid>
        <Grid item xs={12} md={6} data-aos="fade-up">
          <Grid container spacing={2}>
            {data.map((item, index) => (
              <Grid item xs={4} key={index}>
                <CardBase withShadow liftUp  >
                  <Image
                    src={item.logo}
                    alt={item.name}
                    className={classes.logo}
                    lazy={false}
                  />
                </CardBase>
              </Grid>
            ))}
          </Grid>
        </Grid>
      </Grid>
    </div>
  );
};

Partners.propTypes = {
  /**
   * External classes
   */
  className: PropTypes.string,
  /**
   * data to be rendered
   */
  data: PropTypes.array.isRequired,
};

export default Partners;
