
import React, { memo } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import { colors } from '@material-ui/core';
import Countdown from 'react-countdown';

const useStyles = makeStyles(theme => ({
    root: {
        width: '100%',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-evenly',
        flexWrap : 'wrap',
        // width: 'fit-content',
        backgroundColor: 'rgb(82 57 57 / 50%)',
        padding: theme.spacing(2.5),
        borderRadius: theme.spacing(1),
        marginBottom: theme.spacing(2),
        [theme.breakpoints.down('sm')]: {
            padding: theme.spacing(0.5, 2),
            flexDirection: 'column',
            marginBottom: theme.spacing(1)
        },
        [theme.breakpoints.down('md')]: {
            padding: theme.spacing(0.5, 2),
            flexDirection: 'column',
            marginBottom: theme.spacing(1)
        },
        // flexWrap: 'wrap'
        zIndex: '10px'
    },
    container: {
        display: 'flex',
        flexDirection: 'row !important',
        justifyContent: 'space-between',
        color: colors.deepOrange[500],
        [theme.breakpoints.down('sm')]: {
            paddingLeft: theme.spacing(1)
        }
    },
    timeCounter: {
        display: 'flex',
        flexDirection: 'row',
        fontSize: 14,
        fontWeight: 'bold',
        textTransform: 'uppercase',
        [theme.breakpoints.down('sm')]: {
            fontSize: 12
        }
    },
    title: {
        fontSize: 14,
        textTransform: 'uppercase',
        [theme.breakpoints.down('sm')]: {
            fontSize: 12
        }
    },
    educationContainer: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center'
    },
    date: {
        [theme.breakpoints.down('md')]: {
            justifyContent: 'center',
        },
        display: 'flex',
        alignItems: 'center',
        marginTop: theme.spacing(2),
        textOverflow: 'ellipsis',
        whiteSpace: 'nowrap',
        overflow: 'hidden',
        fontWeight: 300,
        [theme.breakpoints.down('xs')]: {
            marginBottom: theme.spacing(0),
            fontSize: 12
        }
    },
    notificationContainer: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        marginLeft: theme.spacing(2),
        [theme.breakpoints.down('xs')]: {
            fontSize: 12
        }
    },
    dividerHeight: {
        [theme.breakpoints.down('md')]: {
            display: 'none'
        },
        height: theme.spacing(6),
        // margin: theme.spacing(2),
        backgroundColor: theme.custom.palette.lightGrey
    },
    upComingLiveDateContainer: {
        display: 'flex',
        alignItems: 'center',
        flexDirection: 'column',
        paddingRight: theme.spacing(1),
        paddingLeft: theme.spacing(1),
        marginRight: theme.spacing(1),
        backgroundColor: 'rgb(82 57 57 / 20%)',
        borderRadius: theme.spacing(1),
    },
    dateValue: {
        [theme.breakpoints.down('sm')]: {
            fontSize: theme.spacing(3),
            width : 'auto'
        },
        color: theme.custom.palette.hover,
        fontWeight: 'bold',
        fontSize: theme.spacing(6),
        width : '75px'
    },
    horizontalDivider: {
        minWidth: '268px',
        marginTop: theme.spacing(2),
        backgroundColor: theme.custom.palette.lightGrey
    },
    scheduleContainer: {
        [theme.breakpoints.up('md')]: {
            width: '50%',
        },
        [theme.breakpoints.down('md')]: {
            width: '100%',
        },
        display: 'flex',
        justifyContent: 'space-between',
        flexDirection: 'column'
    }
}));

const addZeroTime = (time) => time < 10 ? `0${time}` : time;

const TimeCount = ({ deadTimeStamp, title, subTitle}) => {
    const classes = useStyles();
    const currnectTimeStamp = new Date().getTime() / 1000
    // const deadTimeStamp = 1626342333
    const timeRender = ({ days, hours, minutes, seconds, completed }) => {
        if (!completed) {
            return (
                <div className={classes.container}>
                    <Typography component='div' className={classes.timeCounter}>
                        {!!days &&
                            <Typography component='div' className={classes.upComingLiveDateContainer}>
                                <Typography className={classes.dateValue} align='center'>
                                    {days > 9 ? days : `0${days}`}
                                </Typography>
                                <Typography color='textPrimary'>
                                    DAYS
                                </Typography>
                            </Typography>}
                        {<Typography component='div' className={classes.upComingLiveDateContainer}>
                            <Typography className={classes.dateValue} align='center'>
                                {addZeroTime(hours)}
                            </Typography>
                            <Typography color='textPrimary'>
                                HOURS
                            </Typography>
                        </Typography>}
                        {<Typography component='div' className={classes.upComingLiveDateContainer}>
                            <Typography className={classes.dateValue} align='center'>
                                {addZeroTime(minutes)}
                            </Typography>
                            <Typography color='textPrimary'>
                                MINUTES
                            </Typography>
                        </Typography>}
                        <Typography component='div' className={classes.upComingLiveDateContainer}>
                            <Typography className={classes.dateValue} align='center'>
                                {addZeroTime(seconds)}
                            </Typography>
                            <Typography color='textPrimary'>
                                SECONDS
                            </Typography>
                        </Typography>
                    </Typography>
                </div>
            );
        } else {
            return null;
        }
    }

    return (
        <>
            {deadTimeStamp > currnectTimeStamp ?
                <>
                    <main className={classes.root}>
                        <div>
                            <Typography variant={'h4'}>{title}</Typography>
                            <Typography style={{ color: 'rgb(65, 243, 211)' }}> {subTitle} </Typography>
                        </div>
                        <Countdown
                            date={new Date(deadTimeStamp * 1000) || 0}
                            renderer={timeRender}
                            zeroPadTime={3}
                        />

                    </main>
                </>
                : null}
        </>
    );
};

export default memo(TimeCount);
