
import { Contract } from '@ethersproject/contracts'

import { marsABI } from '../../abis/mars';
import { smarsTokenContractAddress } from '../../constants/contractAddresses';

const marsTokenInstance = (account, chainId, library) => {
    if (chainId) {
        return new Contract(smarsTokenContractAddress, marsABI, library.getSigner(account));
    }
    return null
}

export {
    marsTokenInstance
}
