
import React, { useContext, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { AppContext } from 'contexts';
import AOS from 'aos';
import 'aos/dist/aos.css';
import 'react-lazy-load-image-component/src/effects/blur.css';
import 'react-lazy-load-image-component/src/effects/opacity.css';

import Section from 'hoc/Section';
import Hero from './Hero';
import Partners from './Partners';
import SectionAlternate from 'hoc/SectionAlternate';
import Customization from './Customization';
import CustomizationDex from './CustomizationDex';
import Support from './Support';
import TradingViewBoard from './TradingViewBoard';
import SwapDialog from 'components/UI/SwapDialog';
import Download from './Download';
import Roadmap from './Roadmap';
import { integrations, support } from 'utils/helper/mockupData';

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    height: '100%',
    width: '100%',
    justifyContent: 'center',
    alignItems: 'center',
    backgroundImage: 'url(/assets/images/homeBackground.jpeg)',
    backgroundPosition: 'right'
  },
}));

const Home = () => {
  const classes = useStyles();
  const { account, library, chainId } = useContext(AppContext);
  const [isSwapDialog, setIsSwapDialog] = useState(false);

  AOS.init({
    once: true,
    delay: 50,
    duration: 500,
    easing: 'ease-in-out',
  });

  const closeHandler = () => {
    setIsSwapDialog(false)
  }

  return (
    <div className={classes.root}>
      <Section >
        <Hero
          setIsSwapDialog={setIsSwapDialog}
          account={account} />
      </Section>
      <Section>
        <Partners data={integrations} />
      </Section>
      <Section>
        <Customization />
        <CustomizationDex />
      </Section>
      <Section>
        <TradingViewBoard
          setIsSwapDialog={setIsSwapDialog}
          account={account} />
      </Section>
      <SectionAlternate >
        <Support data={support} />
      </SectionAlternate>
      <Section>
        <Download data={[]} />
      </Section>
      <Section  >
        <Roadmap />
      </Section>
      {isSwapDialog &&
        <SwapDialog
          onClose={closeHandler}
          account={account}
          chainId={chainId}
          library={library} />}
    </div >
  );
};

export default Home;
